import React from 'react';
import {connect} from 'react-redux';
import bridge from '@vkontakte/vk-bridge';

import {closePopout, goBack, openModal, openPopout, setPage, setStory} from '../../store/router/actions';

import {Div, Panel, Alert, Group, Button, PanelHeader, Header, Card, CardGrid, Avatar, ScreenSpinner, Placeholder} from "@vkontakte/vkui"

import { Icon16Recent } from '@vkontakte/icons';

class HomePanelBase extends React.Component {

    constructor (props) {
        super(props);

        this.state = {
          popout: null
        }
      }
    
    onClick () {
        this.setState({ popout: <ScreenSpinner /> });
        setTimeout(() => { this.setState({ popout: null }) }, 2000);
    }

    openPopout() {
        this.props.openPopout(
            <Alert
                actions={[{
                    title: 'Нет',
                    autoclose: true,
                    style: 'cancel',
                }, {
                    title: 'Да',
                    autoclose: true,
                    action: {
                        href: 'https://vk.com/cm_daddy'
                    }
                }]}
                onClose={() => this.props.closePopout()}
            >
                <p>Вы действительно хотите перейти по ссылке?</p>
            </Alert>
        );
    }

    render() {
        const {id, setPage, withoutEpic} = this.props;

        return (
            <Panel id={id}>
                <PanelHeader>Главная</PanelHeader>

                <Group>
                    <Placeholder
                            icon={<Icon16Recent width={56} height={56} />}
                            header="Тех. работы"
                        >
                            Данная вкладка не доступна по техническим причинам
                    </Placeholder>
                </Group>
            </Panel>
        );
    }

}

const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal
};

export default connect(null, mapDispatchToProps)(HomePanelBase);
