import React from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux'
import {goBack, closeModal, setStory, openModal} from "./js/store/router/actions";
import {getActivePanel} from "./js/services/_functions";
import * as VK from './js/services/VK';

import {Epic, View, Root, Tabbar, ModalRoot, TabbarItem, ConfigProvider, Counter, ModalCard, Button, Tooltip} from "@vkontakte/vkui";

import { Icon28Profile, 
    Icon28Menu, 
    Icon28HomeOutline,
    Icon56BlockOutline,
    Icon28BookOutline
} from '@vkontakte/icons';

import HomePanelBase from './js/panels/home/base';
import HomePanelGroups from './js/panels/home/groups';

import PayPanelBase from './js/panels/pay/base';

import MorePanelBase from './js/panels/more/base';

import WorkPanelBase from './js/panels/work/base'

import MorePanelExample from './js/panels/more/example';

import HomeBotsListModal from './js/components/modals/HomeBotsListModal';
import HomeBotInfoModal from './js/components/modals/HomeBotInfoModal';

const MODAL_CARD_NEWS = 'news';

class App extends React.Component { 
    constructor(props) {
        super(props);

        this.lastAndroidBackAction = 0;
        this.showsneg = 0;
    }

    componentDidMount() {
        const {goBack, dispatch} = this.props;

        dispatch(VK.initApp());

        window.onpopstate = () => {
            let timeNow = +new Date();

            if (timeNow - this.lastAndroidBackAction > 500) {
                this.lastAndroidBackAction = timeNow;

                goBack();
            } else {
                window.history.pushState(null, null);
            }
        };
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        const {activeView, activeStory, activePanel, scrollPosition} = this.props;

        if (
            prevProps.activeView !== activeView ||
            prevProps.activePanel !== activePanel ||
            prevProps.activeStory !== activeStory
        ) {
            let pageScrollPosition = scrollPosition[activeStory + "_" + activeView + "_" + activePanel] || 0;

            window.scroll(0, pageScrollPosition);
        }
    }

    render() {
        const {goBack, setStory, closeModal, popouts, activeView, activeStory, activeModals, panelsHistory, colorScheme} = this.props;
        
        let history = (panelsHistory[activeView] === undefined) ? [activeView] : panelsHistory[activeView];
        let popout = (popouts[activeView] === undefined) ? null : popouts[activeView];
        let activeModal = (activeModals[activeView] === undefined) ? null : activeModals[activeView];
        
        const homeModals = (
            <ModalRoot activeModal={activeModal}>
                <HomeBotsListModal
                    id="MODAL_PAGE_BOTS_LIST"
                    onClose={() => closeModal()}
                />
                <HomeBotInfoModal
                    id="MODAL_PAGE_BOT_INFO"
                    onClose={() => closeModal()}
                />
                <ModalCard
                    id={MODAL_CARD_NEWS}
                    onClose={() => closeModal()}
                    icon={<Icon56BlockOutline />}
                    header="Данная страница временно не доступна!"
                    actions={
                        <Button size="l" mode="primary" onClick={() => closeModal()}>
                        Я понял
                        </Button>
                    }
                    >
                </ModalCard>
            </ModalRoot>
        );

        return (
            <ConfigProvider transitionMotionEnabled="false" isWebView={true} scheme={colorScheme}>
                <Epic activeStory={activeStory} tabbar={
                    <Tabbar>
                        
                        <TabbarItem
                            onClick={() => setStory('home', 'base')}
                            selected={activeStory === 'home'} text="Главная">
                            <Icon28HomeOutline/>
                        </TabbarItem>

                        <TabbarItem
                            onClick={() => setStory('work', 'base')}
                            selected={activeStory === 'work'} text="ДЗ">
                            <Icon28BookOutline />
                        </TabbarItem>

                        <TabbarItem
                            onClick={() => setStory('more', 'base')}
                            selected={activeStory === 'more'} text="Ещё">
                            <Icon28Menu/>
                        </TabbarItem>

                    </Tabbar>
                }>
                    <Root id="home" activeView={activeView} popout={popout}>
                        <View
                            id="home"
                            modal={homeModals}
                            activePanel={getActivePanel("home")}
                            history={history}
                            onSwipeBack={() => goBack()}
                        >
                            <HomePanelBase id="base" withoutEpic={false}/>
                            <HomePanelGroups id="groups"/>
                        </View>
                    </Root>

                    <Root id="work" activeView={activeView} popout={popout} withoutEpic={false}>
                        <View
                            id="work"
                            modal={homeModals}
                            activePanel={getActivePanel("work")}
                            history={history}
                            onSwipeBack={() => goBack()}
                        >
                            <WorkPanelBase id="base" />
                        </View>
                    </Root>

                    <Root id="more" activeView={activeView} popout={popout} withoutEpic={false}>
                        <View
                            id="more"
                            modal={homeModals}
                            activePanel={getActivePanel("more")}
                            history={history}
                            onSwipeBack={() => goBack()}
                        >
                            <MorePanelBase id="base" />
                        </View>

                        <View
                            id="modal"
                            modal={homeModals}
                            activePanel={getActivePanel("modal")}
                            history={history}
                            onSwipeBack={() => goBack()}
                        >
                            <MorePanelExample id="filters"/>
                        </View>
                    </Root>

                </Epic>
            </ConfigProvider>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        activeView: state.router.activeView,
        activeStory: state.router.activeStory,
        panelsHistory: state.router.panelsHistory,
        activeModals: state.router.activeModals,
        popouts: state.router.popouts,
        scrollPosition: state.router.scrollPosition,

        colorScheme: state.vkui.colorScheme
    };
};


function mapDispatchToProps(dispatch) {
    return {
        dispatch,
        ...bindActionCreators({setStory, goBack, closeModal}, dispatch)
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
