import React from 'react';
import {connect} from 'react-redux';

import {setPage, setStory} from "../../store/router/actions";
import {setActiveTab, setScrollPositionByID} from "../../store/vk/actions";
import {restoreScrollPosition} from "../../services/_functions";

import {
    Div,
    Panel,
    Group,
    CellButton,
    PanelHeader,
    FixedLayout,
    HorizontalScroll,
    TabsItem,
    Alert,
    Tabs,
    Button,
    Snackbar,
    Avatar,
    Header,
    SimpleCell,
    PanelHeaderBack,
    RichCell,
    Footer,
    CardGrid,
    Placeholder
} from "@vkontakte/vkui";

import {
    closePopout, 
    openModal, 
    openPopout
} from '../../store/router/actions';

import { 
    Icon28LogoVkOutline, 
    Icon28LightbulbStarOutline, 
    Icon28HelpOutline, 
    Icon28SnowflakeOutline, 
    Icon28SettingsOutline,
    Icon16ErrorCircle,
    Icon56GhostOutline,
    Icon28UsersOutline,
    Icon16Recent
} from '@vkontakte/icons';

import {View, Root, ModalRoot, ConfigProvider} from "@vkontakte/vkui";

class HomePanelProfile extends React.Component {
    constructor(props) {
        super(props);
    
        this.state = {
            activePanel: 'base'
        };
      }

    componentDidMount() {
        restoreScrollPosition();
    }

    render() {
        const {id, setPage} = this.props;
        const boxStyle = {marginTop: 56};

        return (
            <View transitionMotionEnabled="false" activePanel={this.state.activePanel}>
                
                <Panel id="base">
                    <PanelHeader>
                        Домашнее задание
                    </PanelHeader>

                    <Group>
                        <Placeholder
                            icon={<Icon16Recent width={56} height={56} />}
                            header="Тех. работы"
                        >
                            Данная вкладка не доступна по техническим причинам
                        </Placeholder>
                    </Group>
                </Panel>
            </View>
        );
    }

}

const mapStateToProps = (state) => {
    return {
        activeTab: state.vkui.activeTab,
    };
};

const mapDispatchToProps = {
    setPage,
    setActiveTab,
    setScrollPositionByID,
    openPopout,
    closePopout,
    openModal
};

export default connect(mapStateToProps, mapDispatchToProps)(HomePanelProfile);